.chart-screen {
  .content-area {
    background: $white;
    @media (max-width: 767px) {
      padding: 0;
    }
    @media (max-width: 767px) and (orientation: landscape) {
      height: calc(100vh - -200px);
      //overflow-y: scroll;
    }

  }
}

.eTpeTG {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 12px;
  height: 12px;
  color: #5e5e5e;
}

.cross--btn {
  width: 20px;
  height: 20px;
  align-self: end;
  margin: 10px;
  position: fixed;
  top: 0px;
  right: 0px;
  z-index: 1;
}

.chart-screen__chart-wrapper-table {
  .MuiDialog-paper {
    background: map-get($map: $bg-color, $key: 'light');
  }

  .cross--btn {
    background: map-get($map: $bg-color, $key: 'white');
  }
}
